import styles from '../../styles/docente-dashboard.module.css';

export default function DocenteDashboard() {
  return (
    <div style={{ display: 'flex', minHeight: '100vh', background: '#181A1B' }}>
      {/* Sidebar */}
      <aside className={styles.sidebar}>
        <div className={styles.profile}>
          <img src="/profile-placeholder.png" alt="Docente" className={styles.avatar} />
          <span className={styles.profileName}>Profesora Ana</span>
          <span className={styles.profileId}>ID: 12345</span>
        </div>
        <nav className={styles.menu}>
          <ul className={styles.menuList}>
            <li>
              <a href="#" className={`${styles.menuItem} ${styles.menuItemActive}`}>Notas</a>
            </li>
            {/* Aquí se pueden agregar más opciones en el futuro */}
          </ul>
        </nav>
      </aside>
      {/* Main content */}
      <main className={styles.mainContent}>
        <h1 className={styles.title}>Panel de Control del Docente</h1>
        {/* Aquí puedes agregar secciones como Resumen de Clases, Notificaciones, Accesos Rápidos, etc. */}
        {/* Por ahora solo layout, sin datos estáticos */}
      </main>
    </div>
  );
}
